<script lang="ts">import { onMount } from "svelte/internal";
;
;
import Word from "./Word.svelte";
import { fade } from "svelte/transition";
export let game;
let score = 0;
let typedWord = "";
let input;
let invalidInput = false;
let fallingWords = [];
game.registerWordsListener((updatedList) => {
    fallingWords = updatedList;
});
game.registerScoreListener((newScore) => {
    score = newScore;
});
onMount(() => handleBlur());
const handleBlur = () => input.focus();
const handleKeyUp = (e) => {
    invalidInput = false;
    if (e.key == "Enter") {
        if (game.handle(typedWord))
            typedWord = "";
        else
            invalidInput = true;
    }
};
</script>

<style>
    .playground {
        height: 100vh;
    }

    .words-container {
        height: 100vh;
        width: 100vw;
    }

    .bottom-bar {
        position: absolute;
        bottom: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 1em;
    }

    .input {
        padding: 0.6em 0;
        background: var(--color-primary);
        text-transform: uppercase;
        text-align: center;
        font-size: 1.1em;
        border-radius: var(--radius-high);
        background: var(--color-secondary-transparent);
        transition: transform var(--transition);
    }

    .input.invalid {
        animation: invalid 0.2s forwards linear;
    }

    .score {
        opacity: 0.9;
        padding: 0.5em;
    }

    @keyframes invalid {
        0% {
            transform: translateX(0);
        }
        25% {
            transform: translateX(-10%);
        }
        75% {
            transform: translateX(10%);
        }
        100% {
            transform: translateX(0);
        }
    }
</style>

<div class="playground" transition:fade={{ duration: 500 }}>
    <div class="words-container">
        {#each fallingWords as props (props.id)}
            <Word {props} />
        {/each}
    </div>
    <div class="bottom-bar">
        <div class="score">Score: {score}</div>
        <input
            class="input"
            type="text"
            spellcheck={false}
            bind:value={typedWord}
            bind:this={input}
            on:keyup={handleKeyUp}
            on:blur={handleBlur}
            class:invalid={invalidInput} />
    </div>
</div>
